<template>
  <div>
    <div class="catelist" @click="$router.push('/detail?id='+item.id)" v-for="item in list" :key="item.id">
      <div class="left"><img :src="$pre+item.img" alt=""></div>
      <div class="right">
        <h3>{{item.goodsname}}</h3>
        <h5>&yen;{{item.price | filterPrice}}</h5>
        <br>
        <el-button type="primary" disabled>立即抢购</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { reqGetgoods } from "../../utils/http";
export default {
  data() {
    return {
      list:[]
    }
  },
  mounted() {
    reqGetgoods({fid:this.$route.query.fid}).then(res => {
      this.list = res.data.list;
    });
  }
};
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';
.catelist{
  margin-top .4rem
  display flex
  .left{
    margin-right .6rem
    margin-left .4rem
    width 2rem
    height 2rem
    overflow hidden
    img{
      height 100%
      width 100%
    }
  }
  .right{
    flex 1
    h3{
      line-height $h6
      font-size $h2
      margin-top .1rem
    }
    h5{
      color red
      font-size $h3
    }
  }
}
</style>


